"use client"

import { useState } from "react"
import { DayPicker } from "react-day-picker"
import { Task, importanceColors } from "@/types/task"
import { cn } from "@/lib/utils"

interface TaskCalendarProps {
  tasks: Task[]
}

export function TaskCalendar({ tasks }: TaskCalendarProps) {
  const [month, setMonth] = useState<Date>(new Date())

  const renderTaskBars = (day: Date) => {
    const activeTasks = tasks.filter(
      (task) => day >= task.startDate && day <= task.endDate
    )

    if (activeTasks.length === 0) return null

    return (
      <div className="absolute bottom-0 left-0 right-0 flex flex-col gap-0.5 px-0.5">
        {activeTasks.map((task) => (
          <div
            key={task.id}
            className={cn(
              "h-1 rounded-sm",
              importanceColors[task.importance]
            )}
            title={task.name}
          />
        ))}
      </div>
    )
  }

  return (
    <div className="rounded-md border shadow-sm p-3">
      <DayPicker
        mode="single"
        month={month}
        onMonthChange={setMonth}
        showOutsideDays
        className="w-full"
        classNames={{
          months: "flex flex-col sm:flex-row space-y-4 sm:space-x-4 sm:space-y-0",
          month: "space-y-4",
          caption: "flex justify-center pt-1 relative items-center",
          caption_label: "text-sm font-medium",
          nav: "space-x-1 flex items-center",
          nav_button: "h-7 w-7 bg-transparent p-0 opacity-50 hover:opacity-100",
          table: "w-full border-collapse space-y-1",
          head_row: "flex",
          head_cell: "text-muted-foreground rounded-md w-9 font-normal text-[0.8rem]",
          row: "flex w-full mt-2",
          cell: "relative h-9 w-9 p-0 text-center text-sm focus-within:relative focus-within:z-20",
          day: "h-9 w-9 p-0 font-normal hover:bg-accent",
          day_selected: "bg-primary text-primary-foreground hover:bg-primary hover:text-primary-foreground",
          day_today: "bg-accent text-accent-foreground",
          day_outside: "text-muted-foreground opacity-50",
          day_disabled: "text-muted-foreground opacity-50",
          day_hidden: "invisible",
        }}
        components={{
          Day: ({ date, ...props }) => (
            <div {...props} className="relative h-full w-full p-2">
              <span className="absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2">
                {date?.getDate()}
              </span>
              {date && renderTaskBars(date)}
            </div>
          )
        }}
      />
    </div>
  )
}
