export interface Task {
  id: string
  name: string
  importance: "low" | "medium" | "high"
  startDate: Date
  endDate: Date
}

export type ImportanceColor = {
  [key in Task["importance"]]: string
}

export const importanceColors: ImportanceColor = {
  low: "bg-green-500",    // No tan importante
  medium: "bg-yellow-500", // Importante
  high: "bg-red-500"      // Urgente
}

export const importanceLabels = {
  low: "No tan importante",
  medium: "Importante",
  high: "Urgente"
}
